.hero{
    display: flex;
    justify-content: space-between;
}
.left-h{
    padding: 2rem;
    padding-top: 1.5rem;
    flex: 3;
    display: flex;
    gap: 2rem;
    flex-direction: column;
}
.right-h{
    flex:1;
    position: relative;
    background-color: var(--orange);
}
.the-best{
    margin-top: 4rem;
    background-color:gray;
    border-radius: 4rem;
    width: fit-content;
    padding: 20px 13px;
    text-transform: uppercase;
    color: aliceblue;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;

}
.the-best>span{
    z-index: 2;
}
.the-best>div{
    position: absolute;
    background-color: var(--orange);
    width: 5.4rem;
    height: 80%;
    border-radius: 3rem;
    left: 8px;
    border-radius: 3rem;
    z-index: 1;
}
.hero-text{
    display: flex;
    flex-direction:column;
    gap:1.5rem;
    text-transform: uppercase;
    font-size: 4.5rem;
    font-weight: bold;
    color: aliceblue;
    text-overflow: inherit;

}
.hero-text>div:nth-of-type(3){
    font-size: 1rem;
    font-weight:200;
    text-transform: none;
    letter-spacing: 1px;
    width: 80%;
}
.figure{
    display: flex;
    gap:2rem;

}
.figure>div{
    display: flex;
    flex-direction: column;
}
.figure>div>span:nth-of-type(1){
    color: white;
    font-size: 2rem;
}
.figure>div>span:nth-of-type(2){
    color: var(--gray);
    text-transform: uppercase;
}

.hero-buttons{
    display: flex;
    gap: 1rem;
    font-weight: normal;
}
.hero-buttons>:nth-child(1){
    color: aliceblue;
    background-color: var(--orange);
    width: 8rem;
}
.hero-buttons>:nth-child(2){
    color: aliceblue;
    background-color: transparent;
    width: 8rem;
    border: 2px solid var(--orange);
}
.right-h>.btn{
    position: absolute;
    right: 3rem ;
    top: 2rem;
    color: black;
}
.heart-rate{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: fit-content;
    background-color: var(--darkGrey);
    padding: 1rem;
    align-items: flex-start;
    border-radius: 5px;
    position: absolute;
    right: 4rem;
    top: 7rem;
}
.heart-rate>img{
    width: 2rem;
}
.heart-rate>:nth-child(2){
    color: var(--gray);
}
.heart-rate>:nth-child(3){
    color: white;
    font-size: 1.5rem;
}
.hero-image{
    position: absolute;
    top: 10rem;
    right: 8rem;
    width: 23rem;
    
}
.hero-image-back{
    position: absolute;
    top: 4rem;
    z-index: -1;
    width: 15rem;
    right: 20rem;
}
.calories{
    display: flex;
    gap: 2rem;
    background-color: var(--caloryCard);
    border-radius: 5px;
    padding: 1rem;
    width: 250px;
    position: absolute;
    top: 35rem;
    right: 40rem;

}
.calories>img{
    width: 3rem;
}
.calories>div{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.calories>div>:nth-child(2){
    color: aliceblue;
    font-size: 1.5rem;
}
.calories>div>:nth-child(1){
    color:var(--gray)
}

.girl{
    position: absolute;
    top: 0rem;
    right: 54rem;
    width: 32rem;
    z-index: -1;
    height: 48rem;
    opacity: 80%;
}